<template>
    <div class="page-index wechat" >
        <van-list
            v-if="data"
            v-model="loading"
            :finished="finished"
            finished-text=""
            @load="onLoad"
            direction="up"
            class="vanlist"
        >
            <ul class="list">
                <li class="item" v-for="(item,key) in data" :key="key" >
                    <!-- <P>{{key > 0 && data[key].dateline - data[key-1].dateline }}</P> -->
                    <p class="content_time"  >{{item.dateline | timeformat()}}</p>
                    <!-- <p class="content_time"  v-if="key  == 0 ">{{item.dateline | timeformat()}}</p> -->
                    <div class="content">
                        <div class="right" v-show="item.type == 1">
                        <span class="right_text" v-if="item.attachment == 0">{{item.message}}</span>
                        <img :src="item.message" class="right_img message_img" v-if="item.attachment == 2" @click="ImagePreview(item.message)" alt />
                        <img :src='item.my_avatar' class="right_avatar" alt />
                        </div>
                        <div class="left" v-show="item.type == 2">
                        <img :src='item.user_avatar' class="left_avatar" alt />
                        <span class="left_text" v-if="item.attachment == 0" >{{item.message}}</span>
                        <img :src="item.message" class="left_img message_img" v-if="item.attachment == 2" alt @click="ImagePreview(item.message)" />
                        </div>
                    </div>
                </li>
            </ul>
        </van-list>
        <div class="input-box" :class="{'input-box_pdt':iconShow}">
            <p class="line"></p>
            <div class="flex align-items-center">
                <van-cell-group class="input-message">
                    <van-field  @focus="animateWidth()" v-model="postParameter.message" placeholder="我也来说～" :border="false"   @keyup.enter="addPost" >
                        <span slot="button" style="color: #424E77;" @click="addPost" >发布</span>
                    </van-field>
                </van-cell-group>
                <van-uploader :after-read="afterRead" >
                    <div class="flex align-items-center">
                        <img class="icon icon_image" src="../../assets/images/icon_image.png" >
                    </div>
                </van-uploader>
                <!-- <div class="flex align-items-center" @click="handleIconShow">
                    <img class="icon icon_expression" src="../../assets/images/icon_expression.png" >
                </div> -->
            </div>
            <div class="miley-box" v-if="iconShow">
                <div class="smiley-list-box">
                    <ul class="flex flex-wrap-wrap smiley-list">
                        <li v-for="(item,key) in common_smiley[itemSmileyKey].list" :key="key" @click="handleSmiley(key)" >
                            <img :src="item.show" >
                        </li>
                    </ul>
                </div>
                <ul class="smiley">
                    <li v-for="(item,key) in common_smiley " :key="key">
                        <img :src="item.icon" >
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
let _this;
import {getFromNom} from '@js/moment'
import { Toast,ImagePreview } from 'vant'
export default {
    data(){
        return {
            loading:false,
            finished:false,

            pageInfo:{
                page_size:30,
                page_index:0
            },
            postParameter:{
                message:'',
                attachment:0
            },
            timeArr:[],
            iconShow:false,
            common_smiley:[],
            itemSmileyKey:0,
            data:[]
        }
    },
    created(){
        document.title = this.$route.query.name;
        _this = this;
        this.getSmiley();
      //  this.getChatMsg();
    },
    methods:{
        onLoad(){
            this.getChatMsg();
        },
        // 获取表情
        getSmiley(){
            this.api.getSmiley({

            }).then( res => {
                if(res.err_code == 0){
                    this.common_smiley = res.data.common_smiley
                }
            })
        },
        // 
        handleIconShow(){
            this.iconShow = !this.iconShow; 
        },
        // 获取列表
        getChatMsg(){
            this.api.getChatMsg({
                token:this.$store.state.userData,
                uid:this.$route.query.uid,
                page_size:this.pageInfo.page_size,
                page_index:this.pageInfo.page_index
            }).then(res => {
                this.loading = false;
                if(res.err_code == 0){
                    
                    if(!res.data.length){
                        this.finished = true;
                    }
                    if(this.pageInfo.page_index == 0){
                        this.data = [];
                        setTimeout(()=>{
                             document.getElementsByClassName('vanlist')[0].scrollTo(0,document.getElementsByClassName('list')[0].scrollHeight)
                        },0)
                    }else {
                        setTimeout(()=>{
                             document.getElementsByClassName('vanlist')[0].scrollTo(0,document.getElementsByClassName('list')[0].scrollHeight-200)
                        },0)        
                    }
                    this.timeArr = [];
                    this.data = [...res.data,...this.data,]
                    
                    this.pageInfo.page_index++;
                }else{
                    Toast(res.err_msg);
                    this.finished = true;
                }
            })
        }, 
        // 图片yulan
        ImagePreview(url){
            ImagePreview([url]);
        },
        afterRead(file){
           
            this.addImageByMsg(file.file);
        },
        // 上传图片--私信
        addImageByMsg(myfile){
            let formData = new FormData();
            formData.append('token',this.$store.state.userData)
            formData.append('myfile',myfile)
            this.api.addImageByMsg(formData).then( res => {
                if(res.err_code == 0){
                    this.postParameter.attachment = 2;
                    this.addSendMsg(2,res.data.aid)
                }
            } )
        },
        // 点击表情
        handleSmiley(key){
            console.log(key);
            console.log();
            this.addSendMsg(0,this.common_smiley[this.itemSmileyKey].list[key].show)
        },
        // 发送私聊
        addSendMsg(attachment,message){
            if(this.postParameter.attachment == 0 && !this.postParameter.message) {
                Toast('发布内容不能为空');
            }
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            }); 
            this.api.addSendMsg({
                token:this.$store.state.userData,
                uid:this.$route.query.uid,
                message:message,
                attachment:attachment
            }).then( res => {
                Toast.clear();
                if(res.err_code == 0){
                    this.iconShow = false;
                    this.postParameter.message = ''
                    this.pageInfo.page_index = 0;
                    this.getChatMsg();
                }else{
                    Toast(res.err_msg);
                }   
            })
        }, 
        addPost(){
            this.postParameter.attachment = 0;
            this.addSendMsg(0,this.postParameter.message);
        },
        // 输入框获取焦点
        animateWidth(){
            // setTimeout(function(){ 
            window.scrollTo(0,document.body.scrollHeight)
        
            // }
        }      

    },
    filters:{
        timeformat(v){
            
            if(v){
                    // !_this.timeArr.includes(getFromNom(v*1000))
                    // _this.timeArr.push(getFromNom(v*1000))
                    // console.log( getFromNom(v*1000),v,_this.timeArr)
                    return  getFromNom(v*1000)
            }else {
                return ''
            }
        }
    }  
}
</script>
<style lang="less" scoped>
.page-index{
    height: 100vh;
    position: relative;
    .vanlist{
        height: 100%;
        overflow-y: scroll;
	    -webkit-overflow-scrolling: touch;
    }
    .list{
        padding-bottom: 80px;
        padding: 0 15px 80px;
        li{
            .content_time{
                padding: 20px;
                text-align: center;
                color: #D1D1D1;
                font-size: 12px;
            }
            .content {
                display: flex;
                flex-direction: column;
                width: 100%;
                .message_img{
                    max-width: 180px;
                    max-height: 180px;
                    border-radius: 4px;
                    object-fit: cover;

                }
                .right {
                    display: flex;
                    justify-content: flex-end;
        
                .right_text{
                    font-size: 12px;
                    color: #FFFFFF;
                    background-color: #0EC6F1;
                    border-top-left-radius: 15px;
                    border-bottom-left-radius: 15px;
                    border-bottom-right-radius: 15px;
                    padding: 14px 22.5px;
                    margin: 21px 6.5px 0 0;
                    word-break: break-word;
                    
                }
                .right_img{
                    margin-right: 5px;
                }
                .right_avatar{
                    width: 42px;
                    height: 42px;
                    border-radius: 50%;
                    border: 1px solid;
                    object-fit: cover;
                }
                }
                .left {
                    display: flex;
                    justify-content: flex-start;
                    margin-top: 20px;
                .left_text{
                    font-size: 12px;
                    color: #FFFFFF;
                    background-color: #0EC6F1;
                    border-top-right-radius: 15px;
                    border-bottom-left-radius: 15px;
                    border-bottom-right-radius: 15px;
                    padding: 14px 22.5px;
                    margin: 21px 0 0 6.5px;
                    word-break: break-all;
                }
                .left_img{
                    margin-left: 5px;
                }
                .left_avatar{
                    width: 42px;
                    height: 42px;
                    flex: none;
                    border-radius: 50%;
                    object-fit: cover;
                    border: 1px solid;
                }
                }
            }
        }
    }
    .input-box_pdt{
        padding-bottom: 0px !important;
    }
    .input-box{
        position:absolute;
        width: 100%;
        bottom: 0px;
        padding-bottom: 10px;
        background-color: #ffffff;
        .line{
            height: 1px;
            width: 100%;
            background-color: #888888;
            margin-bottom: 10px;
            transform: scaleY(.5)
        }      
        .input-message{
            // width: 220px;
            width: 280px;
            margin-right: 21px;
            margin-left: 30px;
            // height: 32px;
        }   
        .icon{
            width: 27px;
            height: 27px;
            margin-right: 23px;
        }   
        .icon_expression{
            margin-right: 0px;
        }      
        .miley-box{
            width: 100%;
            padding-bottom: -10px;
            overflow-x: scroll;
            -webkit-overflow-scrolling: touch;
            .smiley-list-box{
                height: 260px;
                overflow-y: scroll;
                -webkit-overflow-scrolling: touch;
                padding: 5px;
                .smiley-list{
                    padding: 0 15px;
                    li{
                        margin-right: 5px;
                        margin-bottom: 5px;
                        img{
                            width: 78px;
                            height: 78px;
                        }
                    }
                }
            }
            .smiley{
                padding: 0 15px;
                background-color: #D4D4D4;
             
                display: flex;

                li{
                    padding: 2px;
                    background-color: #ffffff;
                    img{
                        
                        width: 32px;
                        height: 32px;
                    }
                }
            }
        }
    }
}
</style>
<style >
.wechat .input-box .van-cell{
    background: #D0D0D0;
  
    padding:9px 12px;
    font-size: 15px;
}
</style>